.address_titile{
    text-align: left;
    color: #888;
    background: #fff;
    margin-bottom: 5px;
    span{
        padding: 10px;
        border-bottom: 1px solid #ff5555;
    }
}

.address_box{
    background: #fff;
    padding: 15px 10px;
    font-size: 12px;
    color: #888;

    ul li{
        position: relative;
        padding-left: 80px;
        margin-bottom: 10px;

        label{
            position: absolute;
            left: 0;
            top: 0;
            width: 80px;
            padding: 10px;
            text-align: right;
        }

        input,select{
            width: 100%;
            padding: 10px;
            border: 1px solid #ccc;
            outline: none;
            border-radius: 3px;
        }
    }

    input[type=submit]{
        width: 100%;
        padding: 10px;
        background: #ff5555;
        border: 0;
        outline: none;
        color: #fff;
        border-radius: 3px;
        font-size: 14px;
    }
}